
import ListCompent from '../list/listCompent.vue';
import Scroller from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {API,getQuery} from '../../services';


export default {
components: {
Scroller,
Spinner,
XButton,
Group,
Cell,
LoadMore,
Swiper,
SwiperItem,
ListCompent
},
data() {
return {
swiperItemIndex: 0,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
/* 轮播图数据集合 */
bannerlist: [
'https://static.vux.li/demo/1.jpg',
'https://static.vux.li/demo/1.jpg',
'https://static.vux.li/demo/1.jpg',
],
/* 时间周期对照表 */
timeMap:{1:"日",2:"周",3:"月",4:"季",5:"年"},
/* 当前被选中列表 */
currentType: 0,
/* 分类数据集合 */
typeList: [],
/* 首页热租商品 */
hotGoodsList:[],
/* 首页推荐商品 */
recGoodsList:[],
/* 根据首页商品标签页长度进行商品数据列表缓存，防止重复请求数据 */
goodsList:[],
/* 当前被选中状态的商品信息列表 */
currentGoods:[],
}
},
methods: {
onSwiperItemIndexChange() {

},
/* 进入商品详情 */
goInfo(id){
window.location.href="/#/goodsInfo/"+id;
},
/* 列表点击切换函数 */
/* 根据焦点位置判断 */
typeselect(index,id) {
for (let item of this.typeList) {
item.click = false;
}
/* 传入对应的商品信息id */
this.currentType=index;
this.typeList[index].click = true;
var data = this.typeList;
this.typeList = null;
this.typeList = data;
this.getData(id,index);
},
/* 列表滚动选择函数 */
onScroll(pos) {
this.scrollTop = pos.top
},
/* 根据列表id获取当前列表对应数据 */
getData(id,index){
if(this.goodsList[index]||index==0){
this.currentGoods=this.goodsList[index];
return false;
}
API.main.searchGoodsCategory({
chooseId:id
}).then((Response)=>{
this.goodsList[index]=Response.body.data.shopList.data;
this.currentGoods=this.goodsList[index];
});
},
/* 列表页跳转 */
golist(){
window.location.href="/#/list";
},
/* 跳转到搜索页面 */
gosearch(){
window.location.href="/#/search";
},
changeList() {
this.showList1 = false
this.$nextTick(() => {
this.$refs.scroller.reset({
top: 0
})
})
},
getMoreHot(){
window.location.href="/#/moreHot"
}

},
mounted(){
/* 获取轮播图信息 */
API.main.getBanner().then((Response)=>{
this.bannerlist=Response.body.data.bannerList;
});
/* 获取首页标签 */
API.main.hotTagLabel().then((Response)=>{
let firstLabel={
hot_label_name: "精选",
clcik:true
}
let shopList=Response.body.data.shopList;
for(let item of shopList) {
item.clcik=false;
}
shopList.splice(0,0,firstLabel);
this.goodsList=new Array(shopList.length);
document.querySelector(".main_container .main_typelist_box").style.width=(shopList.length+1)*90+"px";
this.typeList=shopList;
});
/* 获取热门商品 */
API.main.goodsHot({
recomandId:3,
page_number:4
}).then((Response)=>{
this.hotGoodsList=Response.body.data.shopList.data;
});
/* 获取推荐商品 */
API.main.goodsIndexRecom().then((Response)=>{
this.recGoodsList=Response.body.data.shopList.data;
});
}
}



