
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Scroller,
ListCompent,
},
data () {
return {
goodsList:[],
/* 搜索栏目显示的名称 */
goodsVal:"",
categoryId:"",
haveData:false,//是否有未加载的数据
searchType:0,
page:1,
pullupConfig: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
typeList:[
{
name:"默认排序",
select:true,
},
{
name:"销量优先",
select:false,
}
]
}
},

methods:{
routerBack(){
this.$router.goBack();
},
load() {
console.log(1);
if (this.haveData) {
console.log(1);
}
},
/* 选项的切换，切换默认排序和销量优先 */
typeCheck(index){
if(this.typeList[index].select){
return false;
}else{
this.typeList[index].select=true;
this.typeList[1-index].select=false;
}
if(this.categoryId){
API.main.searchGoods({
goods_category_id:this.categoryId,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}else{
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}
},
/* 数据初始化 */
Initialization(){
/* 当前属于从分类中进入的接口*/
if(this.$route.query.categoryId){
/* 搜索栏赋值 */
this.goodsVal=this.$route.query.name;
this.categoryId=this.$route.query.categoryId;
API.main.searchGoods({
goods_category_id:this.$route.query.categoryId,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}else{
/* 来自搜索页面 */
this.goodsVal=this.$route.query.goods_name;
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}
}
}
,mounted(){
this.Initialization();
}
,activated(){
this.Initialization();
}
}



