
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
XTextarea,
Toast
},
data () {
return {
confrim:"请选择地址",
toast:false,
orderType:0,
/* 自提点数据 */
tpl:{},
orderLogistics:'/orderLogistics/',
/* 订单id */
orderId:0,
/* 订单详情数据 */
infoData:{},
/* 时间对照表 */
timeMap:{1:"日",2:"周",3:"月",4:"季",5:"年"},
/* 提示文字 */
currentConfrim:{
title:"等待买家付款",
message:"还有1小时33分自动关闭"
},
titleArray:[
{
title:"等待买家付款",//待付款
message:"还有1小时33分自动关闭"
},
{
title:"等待商家发货",//待发货
message:""
},
{
title:"商家已发货",//待收货
message:"请及时确认收货"
},
{
title:"等待买家付款",//待归还
message:"还有1小时33分自动关闭"
},
{
title:"等待买家付款",//待结算
message:"还有1小时33分自动关闭"
},
{
title:"等待买家付款",//待评价
message:"还有1小时33分自动关闭"
},
{
title:"订单已完成",//评价完成
message:""
},
{
title:"等待买家付款",//订单关闭
message:"还有1小时33分自动关闭"
},
{
title:"退款处理成功",//退款结束
message:"退款金额将及时返还至您的付款账户"
},
{
title:"等待买家付款",//退货完成
message:"还有1小时33分自动关闭"
},
{
title:"等待买家付款",//退货待结算
message:"还有1小时33分自动关闭"
},
{
title:"等待买家付款",//退货结算待确认
message:"还有1小时33分自动关闭"
},
{
title:"等待买家付款",//退款处理中
message:"还有1小时33分自动关闭"
},
{
title:"结算单已出",//结算待确认
message:"请及时确认结算单，还有71小时41分自动结算"
},
]
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/* 返回初始和结束周期 */
rentRange(){
let self=this;
let start=dateFormat(new Date(self.infoData.order_goods_rent_time*1000), 'YYYY-MM-DD');
let end=dateFormat(new Date(self.infoData.order_goods_return_time*1000), 'YYYY-MM-DD');
return {start,end};
},
/* 返回周期名称 */
/* 返回当前周期文字 */
timeText(){
return this.timeMap[this.infoData.rentType];
},
pay_type(){
if(this.infoData.pay_type==1){
return "微信"
}
if(this.infoData.pay_type==2){
return "支付宝"
}
},
returnTpl(){
if(this.infoData.order_express_type==1){
return "快递";
}else if(this.infoData.order_express_type==2){
return "上门";
}else{
return "自提";
}
}
},
methods:{
routerBack(){
this.$router.goBack();
},
/* 获取当前订单类型 */
getOrderType(item){
if (item.order_is_delect == 0) {
if (item.orderRefundStatus == 0) {
if (item.orderStatus == 1) {
if(item.orderShippingStatus == 8){
item.orderType=8;//订单关闭
}else{
item.orderType=1;//代付款
}
} else if (item.orderStatus == 2) {
switch (item.orderShippingStatus) {
case 1:
item.orderType=2;//代发货
break;
case 2:
item.orderType=3;//待收货
break;
case 3:
item.orderType=4;//待归还
break;
case 4:
item.orderType=5;//待结算
break;
case 5:
item.orderType=14;//结算待确认
break;
case 6:
item.orderType=6;//待评价
break;
case 7:
item.orderType=7;//评价完成
break;
}
}
} else if (item.orderRefundStatus == 1) {
item.orderType=9;//退款结束
} else if (item.orderRefundStatus == 2) {
if (item.orderStatus == 2) {//退款退货
if (item.orderShippingStatus == 2) {
item.orderType=11;//退货待结算
} else if (item.orderShippingStatus == 3) {
item.orderType=11;//退货待结算
} else if (item.orderShippingStatus == 5) {
item.orderType=12;//退货待确认结算
} else if (item.orderShippingStatus == 6) {
item.orderType=10;//退货完成
}
}
} else if (item.orderRefundStatus == 4) {
if (item.orderShippingStatus == 6) {
item.orderType=10;//退货完成
}
}else if(item.orderRefundStatus == 3){
item.orderType=13;//退款处理中
}
}
},
Initialization(){
API.order.orderInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderId:this.orderId,
}).then((res)=>{
if(res.body.code==200){
this.infoData=res.body.data;
this.getOrderType(this.infoData);
this.currentConfrim=this.titleArray[this.infoData.orderType-1];
}
});
},
/* 删除订单 */
deletOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要删除该订单吗',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="删除成功";
self.toast=true;
self.getTypeData();
}
});
}
});

},
/* 订单付款 */
payOrder(id){
let self=this;
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.toast=true;
}
});
},
/* 确认收货 */
confrimOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认收货',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderReceipt({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="确认收货成功";
self.getTypeData();
self.toast=true;
}
});
}
});
},
/* 确认结算 */
confrimSettlement(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认结算',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderSettle({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="结算成功";
self.getTypeData();
self.toast=true;
}
});
}
});
},
/* 查看结算单 */
seeSettlement(id){
window.location.href="/#/settlement?id="+id;
},
/* 提醒发货 */
remind(id){

}

},mounted(){

},
activated(){
this.orderId=this.$route.params.id;
this.Initialization();
}
}



